import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'claim',
    pathMatch: 'full'
  },
  {
    path: 'claim',
    loadChildren: () => import('../pages/claim/claim.module').then( m => m.ClaimPageModule)
  },
  {
    path: 'claim-form',
    loadChildren: () => import('../pages/claim-form/claim-form.module').then( m => m.ClaimFormPageModule)
  },
  {
    path: 'claim-tnc',
    loadChildren: () => import('../pages/claim-tnc/claim-tnc.module').then( m => m.ClaimTncPageModule)
  },
  {
    path: 'claim-form-event',
    loadChildren: () => import('../pages/claim-form-event/claim-form-event.module').then( m => m.ClaimFormEventPageModule)
  },
  {
    path: 'claim-form-doc',
    loadChildren: () => import('../pages/claim-form-doc/claim-form-doc.module').then( m => m.ClaimFormDocPageModule)
  },
  {
    path: 'claim-form-review',
    loadChildren: () => import('../pages/claim-form-review/claim-form-review.module').then( m => m.ClaimFormReviewPageModule)
  },
  {
    path: 'claim-form-success',
    loadChildren: () => import('../pages/claim-form-success/claim-form-success.module').then( m => m.ClaimFormSuccessPageModule)
  },
  {
    path: 'claim-history',
    loadChildren: () => import('../pages/claim-history/claim-history.module').then( m => m.ClaimHistoryPageModule)
  },
  {
    path: 'claim-history-detail/:id',
    loadChildren: () => import('../pages/claim-history-detail/claim-history-detail.module').then( m => m.ClaimHistoryDetailPageModule)
  },
  {
    path: 'upload-doc',
    loadChildren: () => import('../pages/upload-document/upload-document.module').then( m => m.UploadDocumentPageModule)
  },
  {
    path: 'upload-doc/history',
    loadChildren: () => import('../pages/upload-document/upload-document.module').then( m => m.UploadDocumentPageModule)
  },
  {
    path: 'sample-document',
    loadChildren: () => import('../pages/sample-document/sample-document.module').then( m => m.SampleDocumentPageModule)
  },
  {
    path: 'previewuploaded/:id',
    loadChildren: () => import('../pages/previewuploaded/previewuploaded.module').then( m => m.PreviewuploadedPageModule)
  },
  {
    path: 'previewuploaded/history/:id',
    loadChildren: () => import('../pages/previewuploaded/previewuploaded.module').then( m => m.PreviewuploadedPageModule)
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
